	},
	methods: {
		openserch(){
			uni.navigateTo({
				url:"../search/search?searchType=topic"
			})
		},
		//初始化数据
		__init(){
			this.getswiper()
			this.gethot()
			this.getnav()
		},
		//获取广告
		async getswiper(){
			let [err,res]=await this.$http.get('/adsense/0')
			if(!this.$http.errorcheck(err,res))return
			let arr=[]
			for(var i=0;i<res.data.data.list.length;i++){
				arr.push({
					src:res.data.data.list[i].src,
					url:res.data.data.list[i].url
				})
			}
			this.topic.swiper=arr
		},
		//获取热门分类
		async getnav(){
			let [err,res]=await this.$http.get('/topicclass')
			if(!this.$http.errorcheck(err,res))return
			let arr=[]
			let list=res.data.data.list
			for(var i=0;i<list.length;i++){
				arr.push({
					id:list[i].id,
					name:list[i].name
				})
			}
			this.topic.nav=arr
		},
		//获取热门话题
		async gethot(){
			let arr=[]
			let [err,res]=await this.$http.get('/hottopic')
			if(!this.$http.errorcheck(err,res))return
			let list=res.data.data.list
			for(var i=0;i<list.length;i++){
				arr.push({
					id:list[i].id,
					titlepic:list[i].titlepic,
					title:list[i].title,
					desc:list[i].desc,
					totalnum:list[i].post_count,
					todaynum:list[i].todaypost_count
				})
			}
			this.topic.list=arr
		},
		changetab(index){
			this.tabindex=index
		},
		tabchange(e){
			this.tabindex=e.detail.current
		},
		load(){
			if(this.guanzhu.loadtext!=="上拉加载更多"){
				return;
			}
			this.guanzhu.loadtext="加载中..."
			setTimeout(()=>{
