						goodnum:20,
						commentnum:50
					}	
				]
				this.guanzhu.list=this.guanzhu.list.concat(obj)
				this.guanzhu.loadtext="上拉加载更多"
			},1000)
		}
	},
	onReachBottom(){
		if(this.topic.loadtext!=="上拉加载更多"){return}
		this.topic.loadtext="加载中..."
		setTimeout(()=>{
			let arr=[
